    <div>
        <div v-for="(image, index) in images" :key="index">
            <img :src="'https://github.com/wubi0116/wubi0116.github.io/blob/master/images/' + image + '.png?raw=true'" class="dialog" style="opacity: 0;" @click="set_dialog_index()">
        </div>

        <div class="pudding" @click="set_pudding_shake()">
            <div class="pudding__part">
                <div class="pudding__head"></div>
                <div class="pudding__eye"></div>
                <div class="pudding__mouse"></div>
                <div class="pudding__cream"></div>
                <div class="pudding__cherry"></div>
                <div class="pudding__bottom"></div>
            </div>

            <div class="pudding__sara"></div>
        </div>
        
        <v-alert :value="true" color="info" icon="check_circle" outline class="hint">
            點點看布丁，會有意想不到的驚喜 :)
        </v-alert>
    </div>
</template>


<script>
export default {
	data: function() {
		return {
            pudding_shake: false,
            count: 0,
            images: ['click-me', 'hbd'],
		}
	},

	created: function() {

	},
	
	components: {
	},
	
	computed: {
		
	},
	
	watch: {

	},

	methods: {
        set_dialog_index: function() {
            let dialogs = document.getElementsByClassName("dialog");
            for(let i=0;i<dialogs.length;i++) {
                let dialog = dialogs[i];
                dialog.style = 'opacity: 0;';
            }
            
            let dialog = document.getElementsByClassName("dialog")[this.count];
            dialog.style = 'opacity: 1;';

            this.count += 1;
        },

		set_pudding_shake: function() {
			this.pudding_shake = !this.pudding_shake;
			
			let pudding = document.getElementsByClassName("pudding")[0];
			if(this.pudding_shake) {
				pudding.classList.add("pudding_clicked");
			}
			else {
				pudding.classList.remove("pudding_clicked");
			}
		}
	}
  }
</script>


